@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/react-star-wars/planets/1/static/media/bg1.11df51a6.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card-link{text-decoration:none;color:unset}.planets-buttons a{margin:20px 8px;text-decoration:none}.header{display:flex;justify-content:center;margin:40px 0}.logo{width:auto;height:200px}.loader-container{text-align:center}.planets-preview{display:flex;justify-content:center;color:white}.planets-preview img{width:666px;height:auto}.planets-preview h2{color:white;font-size:100px;text-transform:uppercase;font-family:"Bebas Neue", cursive;letter-spacing:22px;margin:60px 0px}.planets-preview .planets-preview-desc ul{list-style:none;padding:0}.planets-preview .planets-preview-desc ul li{font-size:18px;margin:20px 0px}.planets-preview .planets-preview-btn{display:flex;justify-content:flex-start}.planets-preview .planets-preview-btn a{border-left:2px solid yellow;padding:20px;cursor:pointer;text-decoration:none;color:white}.planets-list-wrapper{display:flex;align-items:center}.planets-list-wrapper a{margin:0 40px}.planets-list-wrapper .planets-list{display:flex;flex-wrap:wrap;text-decoration:none}.planets-list-wrapper .planets-list .planet-item{opacity:0.7;display:flex;flex:20% 1;cursor:pointer;transition:ease-in-out 0.5s}.planets-list-wrapper .planets-list .planet-item h2,.planets-list-wrapper .planets-list .planet-item p{color:white}.planets-list-wrapper .planets-list .planet-item img{width:100px;height:auto}.planets-list-wrapper .planets-list .active{opacity:1;transition:ease-in-out 0.5}.btn-disabled{opacity:0.5;cursor:default}.btn-next{background-image:url(/react-star-wars/planets/1/static/media/arrow-right.64bc2ab1.svg)}.btn-prev{background-image:url(/react-star-wars/planets/1/static/media/arrow-left.d2b4f277.svg)}.planet-btn{background-repeat:no-repeat;background-size:contain;background-position:center;width:75px;height:100px}.famous-residents .famous-residents-heading{text-align:center;color:white;font-size:100px;text-transform:uppercase;font-family:"Bebas Neue", cursive;letter-spacing:22px;margin:60px 0px}.no-data{color:white}.famous-residents-list{display:flex;flex-wrap:wrap}.resident-card{display:flex;flex-direction:column;padding:1rem}@media (min-width: 40rem){.resident-card{width:50%}}@media (min-width: 56rem){.resident-card{width:33.3333%}}.resident-card h2{color:white}.resident-card .resident-card-img{background-position:center center;background-repeat:no-repeat;background-size:cover;-webkit-filter:contrast(70%);filter:contrast(70%);overflow:hidden;position:relative;transition:-webkit-filter 0.5s cubic-bezier(0.43, 0.41, 0.22, 0.91);transition:filter 0.5s cubic-bezier(0.43, 0.41, 0.22, 0.91);transition:filter 0.5s cubic-bezier(0.43, 0.41, 0.22, 0.91), -webkit-filter 0.5s cubic-bezier(0.43, 0.41, 0.22, 0.91)}.resident-card .resident-card-img::before{content:"";display:block;padding-top:56.25%}@media (min-width: 40rem){.resident-card .resident-card-img::before{padding-top:66.6%}}.resident-card .resident-card-img:hover{-webkit-filter:contrast(100%);filter:contrast(100%)}

